import { AppServiceProvider, AppGlobal } from './../../providers/app-service/app-service';
import { NetworkServiceProvider } from './../../providers/network-service/network-service';
import { BasePage } from './../base/base';
import { IonicPage, NavController, NavParams, ToastController, MenuController } from 'ionic-angular';
import { Component, ViewChild, ElementRef } from '@angular/core';

/**
 * Generated class for the MapPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
var map;//地图对象
declare const AMap: any;//声明
declare const AMapUI: any;
var districtExplorer;
declare const $;
@IonicPage()
@Component({
  selector: 'page-map',
  templateUrl: 'map.html',
})
export class MapPage extends BasePage {
  @ViewChild('map_container') map_container: ElementRef;
  viewBtnFlag = false;
  viewTypeText: string = "全部";
  offlineColor: string = "#999";
  siteType: any = '99';//站点类型
  markers: Array<any> = [];//点位
  siteDetailsList: Array<any> = [];//初始站点详情
  siteDetails: Array<any> = [];
  zoomend: any = 1;
  treeId: any = '';
  trees: any = '';
  mapMenu: boolean = false;
  role: any;
  siteSelection: boolean;


  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl: ToastController,
    public net: NetworkServiceProvider,
    public menuCtrl: MenuController) {
    super(navCtrl, navParams, toastCtrl);

  }
  siteTypes: any = [
    { id: '99', name: "全部" },
    { id: '2', name: "空气站" },
    { id: '3', name: "污染源站" },
    { id: '1', name: "水质自动站" },
    { id: '4', name: "污水处理厂" },

    // { id: '4', name: "汽车尾气" },
    // { id: '5', name: "生物毒性" },
    // { id: '7', name: "地下水" },
    // { id: '8', name: "油烟监测" },
    // { id: '9', name: "土壤污染放射源" },
    // { id: '10', name: "水污染" },
  ];
  ionViewDidLoad() {
    console.log('ionViewDidLoad MapPage');

    this.siteSelections();
    this.getSiteTree();
    this.getMapData();
    this.load();

  }

  siteSelections() {
    if (AppServiceProvider.getInstance().userInfo.role) {
      this.role = AppServiceProvider.getInstance().userInfo.role[0];
      console.log("role :  " + this.role);
      if (this.role == '1' || this.role == '77') {
        this.siteSelection = true;
        this.siteType = '99';
      } else {
        if (this.role == '78') {
          this.siteType = '1';
        } else if (this.role == '79') {
          this.siteType = '3'
        } else if (this.role == '81') {
          this.siteType = '2'
        } else if (this.role = '82') {
          this.siteType = '4'
        }
        this.siteSelection = false;
      }
    }
  }
  doRefresh() {
    this.viewBtnFlag;
    this.viewTypeText = "全部";
    this.offlineColor = "#999";
    this.siteType = '99';//站点类型
    this.markers = [];//点位
    this.siteDetailsList = [];//初始站点详情
    this.siteDetails = [];
    this.zoomend = 1;
    this.treeId = '';
    this.trees = '';
    this.mapMenu = false;
    this.siteSelections();
    this.getSiteTree();
    this.getMapData();
  }
  query() {
    this.mapMenu = true;
    this.menuCtrl.enable(true, "mapMenu");
    this.menuCtrl.toggle("mapMenu");
  }
  getSiteTree() {
    let param: any = {};
    param.userId = AppServiceProvider.getInstance().userInfo.userId;
    param.code = AppServiceProvider.getInstance().userInfo.code;
    this.net.httpPost(
      AppGlobal.API.getSiteTree,
      param, res => {
        this.trees = res;
        this.tree();
      }, error => {
        this.toast(error);
      }
    )
  }
  tree() {
    var treeMapHtml = document.getElementById("treeMapHtml");
    var jsTreeMap = document.getElementById("jsTreeMap");
    treeMapHtml.removeChild(jsTreeMap);
    treeMapHtml.innerHTML = "<div id='jsTreeMap' class='test-1'></div> ";
    let type;
    console.log(this.siteType);

    if (this.siteType == '2') {
      type = "空气站"
    } else if (this.siteType == '3') {
      type = "涉气污染源"
    } else if (this.siteType == '1') {
      type = "水质站"
    } else if (this.siteType == '4') {
      type = "污水处理厂"
    }
    this.treeId="";
    console.log(this.trees[type]);

    $('#jsTreeMap').jstree({
      'core': {
        "themes": {
          "dots": false // no connecting dots between dots
        },
        'data': this.trees[type]
      },
      "plugins": ["search"]
    });
    $('#jsTreeMap').jstree().open_all();
    $('#jsTreeMap').on("changed.jstree", (event, data) => {
      var str = data.node.id;
      console.log(str)
      if (str.slice(0, 1) != "-") {
        this.treeId = str;
      } else {
        this.treeId = '';
      }
    });
  }

  determine() {
    //console.log('进来了')
    console.log('siteType：' + this.siteType)
    console.log('treeId：' + this.treeId)
    this.siteDetails = [];
    map.clearMap();
    if (this.treeId == '') {
      if (this.siteType != '99') {
        this.choiceType();
      } else {
        this.siteDetails = this.siteDetailsList;
        this.addMarker();
      }
    } else {
      this.choiceCode();
    }
    if (this.mapMenu) {
      this.menuCtrl.enable(true, "mapMenu");
      this.menuCtrl.toggle("mapMenu");
    }
  }


  load() {
    map = new AMap.Map(this.map_container.nativeElement, {
      view: new AMap.View2D({//创建地图二维视口
        center: [113.107681, 28.783909],
        zoom: 12, //设置地图缩放级别
        rotateEnable: true,
      })
    });
    map.setMapStyle("amap://styles/blue")
  }
  getMapData() {
    let param: any = {};
    param.userId = AppServiceProvider.getInstance().userInfo.userId;
    param.code = AppServiceProvider.getInstance().userInfo.code;
    this.net.httpPost(
      AppGlobal.API.getMapData, param, res => {
        this.siteDetailsList = res;
        console.log("$$$$$$$$$$$$$$$")
        console.log(this.siteDetailsList)

        this.determine();
      }, error => {
        this.toast(error);
      }
    )
  }

  choiceCode() {
    console.log(this.siteDetailsList);
    for (let site of this.siteDetailsList) {
      if (site.siteId == this.treeId) {
        this.siteDetails.push(site);
      }
    }
    console.log(this.siteDetails);
    this.addMarker();
  }
  choiceType() {
    for (let site of this.siteDetailsList) {
      if (site.siteType == this.siteType) {
        this.siteDetails.push(site);
      }
    }
    this.addMarker();
  }
  zoomendEvent() {
    map.on("zoomend", res => {
      map.clearMap();
      if (map.getZoom() > 10) {
        this.zoomend = 2;
      } else {
        this.zoomend = 1;
      }
    })
    //this.judge();
  }
  proIntDivS(date) {
    console.log("siteType:" + date.siteType + "    queryTime:" + date.queryTime);
    let icon = '';
    if (date.siteType == "1" && date.tails.queryTime && date.tails.pollutant) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan-warn.png"></li><li class="p2"><img src="./assets/imgs/map/autosite-warn.png"></li></ul>';
    } else if (date.siteType == "1" && date.tails.queryTime && (date.tails.pollutant == null || date.tails.pollutant == "")) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan.png"></li><li class="p2"><img src="./assets/imgs/map/autosite-online.png"></li></li></ul>';
    } else if (date.siteType == "1" && (!date.tails.queryTime)) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan-offline.png"></li><li class="p2"><img src="./assets/imgs/map/autosite-offline.png"></li></ul>';
    } else if (date.siteType == "2" && date.tails.queryTime && date.tails.pollutant) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan-warn.png"></li><li class="p2"><img src="./assets/imgs/map/sensor.png"></li></ul>';
    } else if (date.siteType == "2" && date.tails.queryTime && (date.tails.pollutant == null || date.tails.pollutant == "")) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan.png"></li><li class="p2"><img src="./assets/imgs/map/sensor.png"></li></ul>';
    } else if (date.siteType == "2" && (!date.tails.queryTime)) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan-offline.png"></li><li class="p2"><img src="./assets/imgs/map/offline.png"></li></ul>';
    } else if (date.siteType == "3" && date.tails.queryTime && date.tails.pollutant) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan-warn.png"></li><li class="p2"><img src="./assets/imgs/map/pollute-warn.png"></li></ul>';
    } else if (date.siteType == "3" && date.tails.queryTime && (date.tails.pollutant == null || date.tails.pollutant == "")) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan.png"></li><li class="p2"><img src="./assets/imgs/map/pollute-online.png"></li></ul>';
    } else if (date.siteType == "3" && (!date.tails.queryTime)) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan-offline.png"></li><li class="p2"><img src="./assets/imgs/map/pollute-offline.png"></li></ul>';
    } else if (date.siteType == "4" && date.tails.queryTime && date.tails.pollutant) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan-warn.png"></li><li class="p2"><img src="./assets/imgs/map/water-plant.png"></li></ul>';
    } else if (date.siteType == "4" && date.tails.queryTime && (date.tails.pollutant == null || date.tails.pollutant == "")) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan.png"></li><li class="p2"><img src="./assets/imgs/map/water-plant.png"></li></ul>';
    } else if (date.siteType == "4" && (!date.tails.queryTime)) {
      icon = '<ul class="test"><li class="p1"><img src="./assets/imgs/map/scan-offline.png"></li><li class="p2"><img src="./assets/imgs/map/water-plant.png"></li></ul>';
    }
    return icon;
  }
  addMarker() {
    console.log(this.siteDetails)
    this.markers = [];
    AMapUI.loadUI(['geo/DistrictExplorer'], function (DistrictExplorer) {
      districtExplorer = new DistrictExplorer({
        map: map
      });
      var adcode = 430600; //地级市 武汉市代码
      districtExplorer.loadAreaNode(adcode, function (error, areaNode) {
        //清除已有的绘制内容
        districtExplorer.clearFeaturePolygons();
        districtExplorer.renderFeature(areaNode.getSubFeatureByAdcode(430681), {
          cursor: 'default',
          bubble: true,
          strokeColor: "#1E9FFF", //线颜色
          strokeOpacity: 1, //线透明度ionic 
          strokeWeight: 1, //线宽
          fillColor: '#E8E8E8', //填充色
          fillOpacity: 0.3, //填充透明度
        });
      });
    });
    for (let site of this.siteDetails) {
      if (!site.lng) {
        continue;
      }
      let lngLat = new AMap.LngLat(site.lng, site.lat);
      let marker;
      let prointDiv = document.createElement('div');
      prointDiv.style.cursor = "pointer";
      prointDiv.innerHTML = this.proIntDivS(site);

      marker = new AMap.Marker({
        position: lngLat,//基点位置
        offset: new AMap.Pixel(-9, -20), // 设置点标记偏移量
        content: prointDiv // 自定义点标记覆盖物内容
      })
      // marker.setLabel({
      //   offset: new AMap.Pixel(20, 20),  //设置文本标注偏移量
      //   content: "<div class='site-name'>" + site.siteName + "</div>", //设置文本标注内容
      // })

      marker.on('click', res => {
        if (site.siteType == '2') {
          this.navCtrl.push('AirDetailsPage', { site: site, id: '1' });
        } else if (site.siteType == '1') {
          this.navCtrl.push('AutomaticDetailsPage', { site: site, id: '1' });
        } else if (site.siteType == '3') {
          this.navCtrl.push('ContaminatedDetailsPage', { site: site, id: '1' });
        } else if (site.siteType == '4') {
          this.navCtrl.push('HandleDetailsPage', { site: site, id: '1' });
        }
      });

      this.markers.push(marker);
    }
    map.add(this.markers);
    //map.setFitView();// 执行定位
    map.setZoom(9.5);
    map.setCenter([113.107681, 28.783909])
  }


  prointDiv() { }
}



